body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "M PLUS Rounded 1c", sans-serif;
}

h1,
button {
  font-family: "Unica One", cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.top_ui {
  z-index: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.bg_image {
  z-index: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  position: absolute;
}

.overlay {
  z-index: 1;
  opacity: 0.5;
  width: 100vw;
  height: 100vh;
  background: black;
  position: absolute;
}

.user_details {
  z-index: 2;
  width: 30vw;
  color: black;
  opacity: 0.9;
  height: 100vh;
  display: flex;
  background: white;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin: 0px 0px 0px 70vw;
}

.user_detail_image {
  z-index: 2;
  width: 25vh;
  height: 25vh;
  color: white;
  align: center;
  object-fit: cover;
  justify-content: center;
}

.name {
  font-size: xx-large;
}

.technology {
  margin-top: -10px;
}

.icon_flex {
  display: flex;
  margin-top: 36px;
}

.icon {
  padding: 12px;
}

@media screen and (max-width: 800px) {
  .user_details {
    margin: 0px;
    opacity: 0.9;
    width: 100vw;
  }
}

.project_ui {
  display: flex;
  margin-top: 4vh;
}

@media screen and (max-width: 800px) {
  .project_ui {
    flex-direction: column;
  }
}

.project_details {
  width: 0px;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.project_image {
  width: 0px;
  flex: 1 1;
  aspect-ration: 1.46875;
}

@media screen and (max-width: 800px) {
  .project_details {
    width: 100vw;
    margin: 16px;
  }
  .project_image {
    width: 100vw;
  }
}

.project_icon {
  width: 64px;
  height: 64px;
  border-radius: 50%;
}

.project_title {
  display: flex;
  align: center;
  align-items: center;
}

.project_name {
  margin: 0px 20px;
  font-size: xx-large;
}

.project_description {
  color: gray;
  font-size: large;
  font-weight: normal;
  margin: 20px 5vw 0px 0px;
}

.techstacks {
  display: grid;
  grid-template-columns: auto auto;
}

.techstack {
  color: gray;
  font-size: large;
  font-weight: normal;
}

.heading {
  font-size: 8vw;
  text-align: center;
  margin-bottom: -20px;
}

.subheading {
  margin-bottom: 8px;
}

.review {
  color: gray;
  margin-right: 5vw;
  font-style: italic;
  font-weight: normal;
}

.button {
  color: black;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
  font-weight: bold;
  padding: 8px 16px;
  border-radius: 20px;
  border: 2px solid gray;
  background-color: white;
}

// Styling for Small Screen sizes
@media screen and (max-width: 800px) {
  .top_ui {
    z-index: 0;
    width: 50vw;
    height: 50vh;
  }

  .user_details {
    z-index: 2;
    width: 100vw;
    color: black;
    opacity: 0.9;
    height: 100vh;
    display: flex;
    background: white;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }

  .user_detail_image {
    z-index: 2;
    width: 25vh;
    height: 25vh;
    color: white;
    align: center;
    object-fit: cover;
    justify-content: center;
  }
}

